import React from 'react';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';

export const DataSectionValue = ({
  name,
  title,
  calculateValue,
  functionState,
  nutritionType,
  renderStatus,
  isMenu,
  unit,
}) => {
  const isMenuNutrition = renderStatus && isMenu;

  const minMaxValue =
    renderStatus &&
    `${Math.round(renderStatus().minValue)} / ${Math.round(
      renderStatus().maxValue
    )}`;

  return (
    <div className='data-value'>
      <p className='data-value__title'>{title}:</p>
      {renderStatus && <p className='data-value__spread'>{minMaxValue}:</p>}
      <p
        className={`data-value__value ${
          isMenuNutrition && renderStatus().status
        }`}
      >
        {calculateValue(functionState, name, nutritionType)} {unit}
        <ArrowDownwardIcon className='data-value__down' />
        <ArrowUpwardIcon className='data-value__up' />
      </p>
    </div>
  );
};
