import React from 'react';
import { DataSectionValue } from '../compontents/DataSectionValue';
import { useNutrientStatusValue } from '../hooks/useNutrientStatusValue';

export const MicroDataSection = ({
  sectionType,
  nutrientTitle,
  nutrientType,
  calculateValue,
  functionState,
  isMenu,
}) => {
  const { renderSimpleNutrientStatus } = useNutrientStatusValue();

  return (
    <div className='recipe-nutrition__micro'>
      <div className='data-section'>
        <h3 className='data-section__title'>{nutrientTitle}</h3>
        <div className='data-section__content'>
          {sectionType.map(({ title, name, unit }) => {
            const renderMacroStatus = () => {
              switch (name) {
                case 'vitaminA':
                  return renderSimpleNutrientStatus(
                    700,
                    3000,
                    900,
                    3000,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'vitaminB3':
                  return renderSimpleNutrientStatus(
                    13,
                    400,
                    13,
                    400,
                    calculateValue(functionState, name, 'vitamins')
                  );
                //! in smaller values like mcg => dont floor the number
                case 'vitaminB12':
                  return renderSimpleNutrientStatus(
                    2.4,
                    2000,
                    2.4,
                    2000,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'vitaminC':
                  return renderSimpleNutrientStatus(
                    80,
                    1500,
                    80,
                    1500,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'vitaminD':
                  return renderSimpleNutrientStatus(
                    10,
                    80,
                    10,
                    80,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'vitaminE':
                  return renderSimpleNutrientStatus(
                    15,
                    1500,
                    15,
                    1500,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'vitaminK':
                  return renderSimpleNutrientStatus(
                    90,
                    310,
                    120,
                    310,
                    calculateValue(functionState, name, 'vitamins')
                  );
                case 'calcium':
                  return renderSimpleNutrientStatus(
                    300,
                    2500,
                    400,
                    2500,
                    calculateValue(functionState, name, 'minerals')
                  );
                case 'potassium':
                  return renderSimpleNutrientStatus(
                    2000,
                    4700,
                    2200,
                    4700,
                    calculateValue(functionState, name, 'minerals')
                  );
                case 'iron':
                  return renderSimpleNutrientStatus(
                    15,
                    45,
                    18,
                    45,
                    calculateValue(functionState, name, 'minerals')
                  );
                case 'magnesium':
                  return renderSimpleNutrientStatus(
                    375,
                    3500,
                    475,
                    3500,
                    calculateValue(functionState, name, 'minerals')
                  );
                case 'sodium':
                  return renderSimpleNutrientStatus(
                    0,
                    1500,
                    0,
                    // 2300,
                    1500,
                    calculateValue(functionState, name, 'minerals')
                  );
                default:
                  return renderSimpleNutrientStatus(
                    0,
                    100,
                    0,
                    100,
                    calculateValue(functionState, name, 'minerals')
                  );
              }
            };

            return (
              <DataSectionValue
                name={name}
                title={title}
                calculateValue={calculateValue}
                functionState={functionState}
                nutritionType={nutrientType}
                renderStatus={renderMacroStatus}
                isMenu={isMenu}
                unit={unit}
              />
            );
          })}
        </div>
      </div>
    </div>
  );
};
