import React, { useState, useEffect } from 'react';
import { useFoodStateValue } from '../context/FoodStateContext';
import { setInputFilter } from '../helpers/setInputFilter';
import { useFoodStateHelpers } from '../hooks/useFoodStateHelpers.js';
import { makeStyles } from '@material-ui/core/styles';
import {
  Button,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  InputLabel,
  Input,
  FormControl,
  TextField,
  Select,
  InputAdornment,
} from '@material-ui/core';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  marginTop: {
    marginTop: '2rem',
  },
  marginRight: {
    marginRight: '0.5rem',
  },
}));

export function GrammageModal({
  title,
  content,
  currentUnit,
  grammageInfo,
  getPlaceholderUnit,
}) {
  const classes = useStyles();
  const [open, setOpen] = useState(false);
  const [grammage, setGrammage] = useState('');
  const [grammageUnit, setGrammageUnit] = useState(
    grammageInfo.type === 'foodstuff' ? 'g' : 'ml'
  );

  const { setFoodState } = useFoodStateValue();
  const { getAvailableFood, getAvailableFoodIndex } = useFoodStateHelpers(
    title
  );

  const getGrammageStateValue = () => {
    return getAvailableFood().grammage.value;
  };

  const handleClose = () => {
    setOpen(false);
    console.log(currentUnit);
    console.log('grammageUnit', grammageUnit);
  };

  //!
  const convertUnitValue = (unit, value, isMain = false) => {
    let convertedValue = value;

    switch (unit) {
      case 'kg' || 'l':
        convertedValue = value * 1000;
        break;
      case 'g' || 'ml':
        convertedValue = value;
        break;
      case 'piece':
        if (grammageInfo.piece) {
          convertedValue = value * grammageInfo.piece;
        }
        break;
      case 'smallPiece':
        convertedValue = value * grammageInfo.smallPiece;
        break;
      case 'cup':
        convertedValue = value * 250;
        break;
      case 'tbs':
        convertedValue = value * 15;
        break;
      case 'ts':
        convertedValue = value * 5;
        break;
      default:
        return convertedValue;
    }

    if (isMain) return convertedValue / 1000;

    return Number(convertedValue);
  };

  const setGrammageState = () => {
    setFoodState((prevState) => {
      return prevState.map((food, index) => {
        if (index === getAvailableFoodIndex()) {
          return {
            ...prevState[index],
            grammage: {
              ...prevState[index].grammage,
              value: Number(grammage),
              valueInMainUnit: convertUnitValue(grammageUnit, grammage, true),
              valueInDefaultUnit: convertUnitValue(grammageUnit, grammage),
              unit: grammageUnit,
            },
          };
        } else {
          return food;
        }
      });
    });

    handleClose();
  };

  useEffect(() => {
    getGrammageStateValue() !== 0 && setGrammage(getGrammageStateValue());

    setGrammageUnit(currentUnit);
  }, []);

  return (
    <div>
      <Button
        className={classes.marginRight}
        variant='contained'
        color='primary'
        onClick={() => setOpen(true)}
      >
        {content}
      </Button>
      <Dialog
        disableBackdropClick
        disableEscapeKeyDown
        open={open}
        onClose={() => setOpen(false)}
      >
        <DialogTitle>{title}</DialogTitle>
        <DialogContent>
          <form className={classes.container}>
            <FormControl className={classes.formControl}>
              <InputLabel htmlFor='demo-dialog-native'>Jednotka</InputLabel>
              <Select
                native
                value={grammageUnit}
                onChange={(e) => {
                  setGrammageUnit(e.target.value || '');
                  console.log('yoyoyoo');
                }}
                input={<Input id='demo-dialog-native' />}
              >
                {grammageInfo.type === 'foodstuff' ? (
                  <>
                    <option value='kg'>Kilogramy</option>
                    <option value='g'>Gramy</option>
                    <option value='piece'>Kus</option>
                    <option value='smallPiece'>Menší Kus</option>
                  </>
                ) : (
                  <>
                    <option value='l'>Litry</option>
                    <option value='ml'>Mililitry</option>
                    <option value='cup'>Sklenice</option>
                    <option value='smallCup'>Malá Sklenice</option>
                    <option value='tbs'>Lžíce</option>
                    <option value='ts'>Lžička</option>
                  </>
                )}
              </Select>
            </FormControl>
            <TextField
              onClick={() =>
                setInputFilter(
                  document.getElementById('outlined-start-adornment'),
                  function (value) {
                    return /^-?\d*$/.test(value);
                  }
                )
              }
              onInput={(e) => {
                setGrammage(e.target.value);
              }}
              label='Zadejte Hmotnost'
              id='outlined-start-adornment'
              className={clsx(classes.marginTop, classes.textField)}
              InputProps={{
                startAdornment: (
                  <InputAdornment position='start'>
                    {getPlaceholderUnit(grammageUnit).replace('x ', '')}
                  </InputAdornment>
                ),
              }}
              value={grammage}
              variant='outlined'
            />
          </form>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color='primary'>
            Zrušit
          </Button>
          <Button onClick={setGrammageState} color='primary'>
            Potvrdit
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
