import React from 'react';
import { useNutrientStatusValue } from '../hooks/useNutrientStatusValue';
import { DataSectionValue } from '../compontents/DataSectionValue';

export const MacroDataSection = ({
  title,
  name,
  subSection,
  calculateValue,
  functionState,
  caloricValue,
  isMenu,
}) => {
  const {
    renderComplexNutrientStatus,
    renderSimpleNutrientStatus,
    renderPercentageNutrientStatus,
  } = useNutrientStatusValue();

  const renderMacroStatus = () => {
    if (name === 'protein')
      return renderComplexNutrientStatus(
        0.8,
        2,
        calculateValue(functionState, name, 'macro')
      );
    if (name === 'carbohydrate')
      return renderComplexNutrientStatus(
        2,
        3.8,
        calculateValue(functionState, name, 'macro')
      );
    if (name === 'totalFat')
      return renderComplexNutrientStatus(
        0.8,
        1.2,
        calculateValue(functionState, name, 'macro')
      );
  };

  return (
    <div className='data-section'>
      <h3 className='data-section__title'>{title}</h3>
      <div className='data-section__content'>
        <DataSectionValue
          name={name}
          title='Celkem'
          calculateValue={calculateValue}
          functionState={functionState}
          nutritionType='macro'
          renderStatus={renderMacroStatus}
          isMenu={isMenu}
          unit='g'
        />
        {subSection.map(({ title, name }) => {
          const renderSubMacroStatus = () => {
            switch (name) {
              case 'fiber':
                return renderSimpleNutrientStatus(
                  21,
                  70,
                  30,
                  81,
                  calculateValue(functionState, name, 'macro')
                );
              case 'sugar':
                return renderSimpleNutrientStatus(
                  0,
                  24,
                  0,
                  36,
                  calculateValue(functionState, name, 'macro')
                );
              case 'saturatedFat':
                return renderPercentageNutrientStatus(
                  7,
                  caloricValue,
                  calculateValue(functionState, name, 'macro')
                );
              case 'monounsaturatedFat':
                return renderPercentageNutrientStatus(
                  20,
                  caloricValue,
                  calculateValue(functionState, name, 'macro'),
                  true
                );
              case 'polyunsaturatedFat':
                return renderPercentageNutrientStatus(
                  10,
                  caloricValue,
                  calculateValue(functionState, name, 'macro'),
                  true
                );
              case 'transFat':
                return renderPercentageNutrientStatus(
                  1,
                  caloricValue,
                  calculateValue(functionState, name, 'macro')
                );
              default:
                return renderSimpleNutrientStatus(
                  0,
                  100,
                  0,
                  100,
                  calculateValue(functionState, name, 'macro')
                );
            }
          };

          return (
            <DataSectionValue
              key={title}
              name={name}
              title={title}
              calculateValue={calculateValue}
              functionState={functionState}
              renderStatus={renderSubMacroStatus}
              nutritionType='macro'
              isMenu={isMenu}
              unit='g'
            />
          );
        })}
      </div>
    </div>
  );
};
