import './App.scss';
import React, {useState, useRef, useEffect } from 'react';
import { useAuthValue } from './context/AuthContext';
import { useLocation } from 'react-router-dom';
import { Switch, Route } from 'react-router-dom';
import {
  RecipePage,
  MenuPage,
  ProfilePage,
  IntroPage,
  LoginPage,
  RegisterPage,
} from './pages/main';
import { Header, BottomNav } from './compontents/layout';
import { PrivateRoute, AlertBanner } from './compontents';

function App() {
  const [hideNav, setHideNav] = useState();
  const header = useRef();
  const { currentUser } = useAuthValue();
  const location = useLocation();

  useEffect(() => {
    if(location.pathname === '/login' || location.pathname === '/register') {
      setHideNav(true);
      console.log('hidden')
    } else {
      setHideNav(false)
    }
  })

  return (
    <Switch>
      <div className='app'>
        <AlertBanner />

        <Header passRef={header} />

        <div className='app__content'>
          <PrivateRoute exact path='/' component={IntroPage} />
          {currentUser && (
            <>
              <Route path='/my-profile'>
                <ProfilePage />
              </Route>
              <Route path='/create-recipe'>
                <RecipePage header={header} />
              </Route>
              <Route path='/my-recipes'>
                <MenuPage header={header} />
              </Route>
            </>
          )}
        </div>

        <BottomNav hideNav={hideNav} />

        <Route path='/login'>
          <LoginPage />
        </Route>
        <Route path='/register'>
          <RegisterPage />
        </Route>
      </div>
    </Switch>
  );
}

export default App;
